import React from 'react';
import { NavLink } from 'react-router-dom';
import { ORIGINAL_SVG_updownicon } from '../../../../utils/defaults';
import generatesvgUrl from '../../../../utils/generatesvgUrl'
interface BioProps {
    bio1: string,
    bio2: string,
    bio3: string,
    bio4: string,

}
const CreateEmail: React.FC<BioProps> = ({ bio1, bio2, bio3, bio4 }): JSX.Element => {
    const svg1 = generatesvgUrl(ORIGINAL_SVG_updownicon)
    const svg = generatesvgUrl(bio4)
    const [showModal, setShowModal] = React.useState(false);

    return (
        <div>
            <div className="flex flex-col w-full mx-auto justify-center mt-4 ">


                <div className="flex flex-col border-dashed border-2 border-opacity-25 border-gray95 px-2 rounded-lg py-4 mb-6">
                    <div className="flex flex-row justify-center">
                        <img src={svg} className="mr-2" />

                        <h1 className="text-indigo text-base">
                            {bio1}
                        </h1>
                    </div>
                    <div className="flex justify-center mt-2">

                        <p className="text-gray95 flex justify-center leading-4 text-center" >{bio2}</p>
                    </div>
                    <NavLink to="/createcard">
                    </NavLink>
                    <button onClick={() => setShowModal(true)}>

                        <div className="bg-indigo flex flex-row border-0 rounded-lg justify-center p-2 mt-4" >
                            <svg className="mt-1 mr-4" width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M7 2V12M12 7L2 7" stroke="#EEF2FF" stroke-width="4" stroke-linecap="round" stroke-linejoin="round" />
                            </svg>

                            <p className="text-white">{bio3}</p>
                        </div>
                    </button>


                    <div>

                        {showModal ? (
                            <>
                                <div
                                    className="justify-center items-center h-full flex overflow-x-hidden overflow-y-auto fixed inset-0 z-50 outline-none focus:outline-none"
                                >
                                    <div className="relative h-5/6 w-auto my-6 mx-auto max-w-4xl">
                                        {/*content*/}
                                        <div className="border-0 rounded-lg shadow-lg relative flex bg-white h-full flex-col w-full  outline-none focus:outline-none">
                                            {/*header*/}
                                            <div className="flex items-start justify-between p-5 border-b border-solid border-blueGray-200 rounded-t">
                                                <div>

                                                    <h3 className="text-3xl text-gray94 font-semibold">
                                                        Select Template
                                                    </h3>
                                                </div>
                                                <div className="flex flex-row">

                                                    <div className="flex flex-row px-2 border-2 border-bordercolor rounded-md  mr-2">
                                                        <button className="pr-2">Popular </button>
                                                        <img className="w-2 " src={svg1} />
                                                    </div>
                                                    <div>

                                                        <button
                                                            className="p-1 ml-auto  border-2 border-black text-black opacity-5 float-right text-3xl leading-none font-semibold outline-none focus:outline-none"
                                                            onClick={() => setShowModal(false)}
                                                        >
                                                            <span className="bg-transparent text-black  h-6 w-6 text-2xl block outline-none focus:outline-none">
                                                                X
                                                            </span>
                                                        </button>
                                                    </div>
                                                </div>

                                            </div>
                                            {/*body*/}
                                            <div className="relative p-6 flex-auto">
                                                <p className="my-4 text-blueGray-500 text-lg leading-relaxed">
                                                    I always felt like I could do anything. That’s the main
                                                    thing people are controlled by! Thoughts- their perception
                                                    of themselves! They're slowed down by their perception of
                                                    themselves. If you're taught you can’t do anything, you
                                                    won’t do anything. I was taught I could do everything.
                                                </p>
                                            </div>
                                            {/*footer*/}
                                            <div className="flex items-center justify-end p-6 border-t border-solid border-blueGray-200 rounded-b">
                                                <button
                                                    className="text-red-500 background-transparent font-bold uppercase px-6 py-2 text-sm outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                                                    type="button"
                                                    onClick={() => setShowModal(false)}
                                                >
                                                    Close
                                                </button>
                                                <button
                                                    className="bg-emerald-500 text-white active:bg-emerald-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                                                    type="button"
                                                    onClick={() => setShowModal(false)}
                                                >
                                                    Save Changes
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="opacity-25 fixed inset-0 z-40 bg-black"></div>
                            </>
                        ) : null}

                    </div>
                </div>

            </div>
        </div >

    )

};

export default CreateEmail;
